/* pager */

/* Pager */
.pagination {
	cursor: pointer;
	display: inline-block;
	margin: 1em 0;
	margin-left: 25em;
}

.pagination a {
	color: black;
	float: left;
	padding: 8px 16px;
	text-decoration: none;
	transition: background-color 0.3s;
}

.pagination a.active {
	background-color: #4caf50;
	color: white;
}

.pagination a:hover:not(.active) {
	background-color: #ddd;
}
body {
	background: #cfc9c9;
	color: #ffffff;
}

.movie-app > .row {
	overflow-x: auto;
	flex-wrap: nowrap;
}

.image-container {
	/* position: relative; */
	transition: transform 0.2s;
}

.image-container:hover {
	cursor: pointer;
	transform: scale(1.1);
}

.image-container:hover .overlay {
	opacity: 1;
}

.overlay {
	position: absolute;
	background: rgba(0, 0, 0, 0.8);
	width: 100%;
	transition: 0.5s ease;
	opacity: 0;
	bottom: 0;
	font-size: 20px;
	padding: 20px;
	text-align: center;
}


.box {
	display: grid;
	grid-gap: 20px;
	grid-template-columns: repeat(6, minmax(100px, 1fr));
  }
  
  .box a {
	transition: transform .3s;
  }
  
  .box a:hover {
	transition: transform .3s;
	transform: scale(1.4);
  }
  
  .box img {
	border-radius: 2px;
  }
